.banner-container {
    background-size: cover;
    background-position: center;
    color: white;
    text-align: center;
    padding: 20px 0;
    position: relative;
    height: 100px;
    /* Add an overlay for better text visibility if needed */
  }

  .banner-container::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.4); /* Adjust the alpha value for darkness */
  }

  .banner-content {
      position: relative;
      z-index: 1; /* Ensure content is above the overlay */
  }

  .banner-content h1 {
    font-size: 2.5em;
    margin-bottom: 10px;
  }

  .banner-content p {
    font-size: 1.2em;
  }